import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import Toast from './Toast';

function Propostas(
  { viewproposta,
    hospital,
    unidade,
    idusuario,
    usuario,
    tipo,
    funcao,
    idatendimento,
    idProposta,
    proposta,
    inicio,
    termino,
  }) {
  //servidor.
  var html = 'https://pulsarapp-server.herokuapp.com';
  const changeState = useDispatch();

  // chave para exibição do componente.
  const [viewcomponent, setviewcomponent] = useState(viewproposta);

  useEffect(() => {
    if (viewproposta !== 0) {
      currentMonth();
      setViewDatePicker(0);
      setdateInicio(inicio ? inicio : moment().format('DD/MM/YYYY'));
      setviewcomponent(viewproposta);
    } else {
    }
  }, [viewproposta])

  // inserindo registro.
  const insertData = () => {
    var proposta = document.getElementById("inputProposta").value.toUpperCase();
    var termino = '';
    if (inicio !== '' && proposta !== '') {
      var obj = {
        idatendimento: idatendimento,
        inicio: dateInicio,
        termino: dateTermino,
        proposta: proposta,
      };
      axios.post(html + '/insertprop', obj).then(() => {
        toast(1, '#52be80', 'PROPOSTA REGISTRADA COM SUCESSO.', 5000);
        setTimeout(() => {
          fechar();
        }, 3000);
      });
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 5000);
    }
  };

  // atualizando registro.
  const updateData = () => {
    var proposta = document.getElementById("inputProposta").value.toUpperCase();
    if (dateInicio !== '' && proposta !== '') {
      var obj = {
        id: idProposta,
        idatendimento: idatendimento,
        inicio: dateInicio,
        termino: dateTermino,
        proposta: proposta,
      };
      axios.post(html + '/updateprop/' + idProposta, obj).then(() => {
        toast(1, '#52be80', 'PROPOSTA ATUALIZADA COM SUCESSO.', 5000);
        setTimeout(() => {
          fechar();
        }, 3000);
      });
    } else {
      toast(1, '#ec7063', 'CAMPOS OBRIGATÓRIOS EM BRANCO.', 5000);
    }
  };

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // DATEPICKER.
  // preparando a array com as datas.
  var arraydate = [];
  const [arraylist, setarraylist] = useState([]);
  // preparando o primeiro dia do mês.
  var month = moment().format('MM');
  var year = moment().format('YY');
  const [startdate] = useState(moment('01/' + month + '/' + year, 'DD/MM/YY'));
  // descobrindo o primeiro dia do calendário (último domingo do mês anteior).
  const firstSunday = (x, y) => {
    while (x.weekday() > 0) {
      x.subtract(1, 'day');
      y.subtract(1, 'day');
    }
    // se o primeiro domingo da array ainda cair no mês atual:
    if (x.month() == startdate.month()) {
      x.subtract(7, 'days');
      y.subtract(7, 'days');
    }
  }
  // criando array com 42 dias a partir da startdate.
  const setArrayDate = (x, y) => {
    arraydate = [x.format('DD/MM/YY')];
    while (y.diff(x, 'days') > 1) {
      x.add(1, 'day');
      arraydate.push(x.format('DD/MM/YY').toString());
    }
  }
  // criando a array de datas baseada no mês atual.
  const currentMonth = () => {
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês anterior.
  const previousMonth = () => {
    startdate.subtract(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }
  // percorrendo datas do mês seguinte.
  const nextMonth = () => {
    startdate.add(30, 'days');
    var month = moment(startdate).format('MM');
    var year = moment(startdate).format('YY');
    var x = moment('01/' + month + '/' + year, 'DD/MM/YY');
    var y = moment('01/' + month + '/' + year, 'DD/MM/YY').add(42, 'days');
    firstSunday(x, y);
    setArrayDate(x, y);
    setarraylist(arraydate);
  }

  const selectInputInicio = () => {
    setinputselect(1);
    setViewDatePicker(1);
  }
  const selectInputTermino = () => {
    setinputselect(2);
    setViewDatePicker(1);
  }

  // exibição do datepicker.
  const showDatePicker = () => {
    setViewDatePicker(1);
  }
  // selecionando uma data de início ou término no datepicker.
  const [inputselect, setinputselect] = useState(0); // 1 para input dataInicio, 2 para input dataTermino.
  const [pickdate, setpickdate] = useState('');
  const [dateInicio, setdateInicio] = useState(moment().format('DD/MM/YYYY'));
  const [dateTermino, setdateTermino] = useState('');
  const selectDate = (date) => {
    setpickdate(date);
    if (inputselect === 1) {
      setdateInicio(date);
    } else {
      setdateTermino(date);
    }
    setViewDatePicker(0);
  }

  const [viewDatePicker, setViewDatePicker] = useState(0);
  // renderização do datepicker.
  function DatePicker() {
    if (viewDatePicker === 1) {
      return (
        <div
          className="secondary"
          style={{
            display: window.innerWidth > 800 ? 'flex' : 'none',
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'fixed',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            margin: 0,
            zIndex: 5,
          }}
        >
          <div
            className="secondary"
            style={{
              position: 'absolute',
              backgroundColor: '#ffffff',
              zIndex: 5,
              padding: 15,
            }}>
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              width: '395',
              alignItems: 'center',
              justifyContent: 'center',
              padding: 5,
            }}>
              <button
                className="blue-button"
                onClick={() => previousMonth()}
                id="previous"
                style={{
                  width: 50,
                  height: 50,
                  margin: 2.5,
                  color: '#ffffff',
                }}
                title={'MÊS ANTERIOR'}
              >
                {'◄'}
              </button>
              <p
                className="title2"
                style={{
                  width: 200,
                  fontSize: 16,
                  margin: 2.5
                }}>
                {startdate.format('MMMM').toUpperCase() + ' ' + startdate.year()}
              </p>
              <button
                className="blue-button"
                onClick={() => nextMonth()}
                id="next"
                style={{
                  width: 50,
                  height: 50,
                  margin: 2.5,
                  color: '#ffffff',
                }}
                title={'PRÓXIMO MÊS'}
              >
                {'►'}
              </button>
            </div>
            <div style={{
              display: 'flex',
              flexDirection: 'row',
              width: '395',
              alignItems: 'center',
              justifyContent: 'center',
              padding: 5,
            }}>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>DOM</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEG</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>TER</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUA</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>QUI</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SEX</p>
              <p className="title2" style={{ width: 50, fontSize: 12, margin: 2.5 }}>SAB</p>
            </div>
            <div
              className="secondary"
              id="LISTA DE DATAS"
              style={{
                display: 'flex',
                flexDirection: 'row',
                flexWrap: 'wrap',
                justifyContent: 'center',
                alignItems: 'center',
                borderRadius: 5,
                margin: 0,
                padding: 5,
                width: 395,
                height: 340,
              }}
            >
              {arraylist.map((item) => (
                <button
                  className="blue-button"
                  onClick={() => selectDate(item)}
                  style={{
                    width: 50,
                    height: 50,
                    margin: 2.5,
                    color: '#ffffff',
                    backgroundColor: item === pickdate ? '#ec7063' : '#1f7a8c',
                    opacity: item.substring(3, 5) === moment(startdate).format('MM') ? 1 : 0.5,
                  }}
                  title={item}
                >
                  {item.substring(0, 2)}
                </button>
              ))}
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
  function DatePickerToMenus() {
    if (viewDatePicker === 1) {
      return (
        <div
          className="secondary"
          style={{
            display: window.innerWidth < 800 ? 'flex' : 'none',
            backgroundColor: '#F8F9F9 ',
            borderRadius: 5,
            margin: 0,
            marginTop: 5,
            marginBottom: 5,
          }}>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            marginTop: 20,
          }}>
            <button
              className="blue-button"
              onClick={() => previousMonth()}
              id="previous"
              style={{
                width: 30,
                minWidth: 30,
                height: 30,
                minHeight: 30,
                margin: 2.5,
                color: '#ffffff',
              }}
              title={'MÊS ANTERIOR'}
            >
              {'◄'}
            </button>
            <p
              className="title2"
              style={{
                width: 180,
                fontSize: 16,
                margin: 2.5
              }}>
              {startdate.format('MMMM').toUpperCase() + ' ' + startdate.year()}
            </p>
            <button
              className="blue-button"
              onClick={() => nextMonth()}
              id="next"
              style={{
                width: 30,
                minWidth: 30,
                height: 30,
                minHeight: 30,
                margin: 2.5,
                color: '#ffffff',
              }}
              title={'PRÓXIMO MÊS'}
            >
              {'►'}
            </button>
          </div>
          <div style={{
            display: 'flex',
            flexDirection: 'row',
            alignItems: 'center',
            justifyContent: 'center',
            padding: 5,
          }}>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>DOM</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>SEG</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>TER</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>QUA</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>QUI</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>SEX</p>
            <p className="title2" style={{ width: 32, fontSize: 12, margin: 2.5 }}>SAB</p>
          </div>
          <div
            className="secondary"
            id="LISTA DE DATAS"
            style={{
              display: 'flex',
              flexDirection: 'row',
              flexWrap: 'wrap',
              justifyContent: 'center',
              alignItems: 'center',
              borderRadius: 5,
              marginBottom: 20,
              padding: 0,
              width: 260,
            }}
          >
            {arraylist.map((item) => (
              <button
                className="blue-button"
                onClick={() => selectDate(item)}
                style={{
                  width: 32,
                  minWidth: 32,
                  height: 32,
                  minHeight: 32,
                  margin: 2.5,
                  color: '#ffffff',
                  backgroundColor: item === pickdate ? '#ec7063' : '#1f7a8c',
                  opacity: item.substring(3, 5) === moment(startdate).format('MM') ? 1 : 0.5,
                }}
                title={item}
              >
                {item.substring(0, 2)}
              </button>
            ))}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  const fechar = () => {
    changeState({
      type: 'CORRIDA_OFF',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
  }

  // renderização do componente.
  if (viewcomponent === 1) { // inserir.
    return (
      <div
        className="secondary fade-in"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'fixed',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          margin: 0,
          zIndex: 5,
        }}
      >
        <DatePicker></DatePicker>
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <div
          className="secondary"
          style={{
            flexDirection: 'column',
            justifyContent: 'center',
            backgroundColor: '#FFFFFF',
            borderRadius: 5,
            padding: window.innerWidth > 800 ? 30 : 10,
          }}
        >
          <label className="title2" style={{ marginTop: 0, fontSize: 16 }}>
            INSERIR PROPOSTA
          </label>
          <div
            className="scroll"
            style={{
              overflowY: window.innerWidth > 800 ? 'hidden' : 'scroll',
              justifyContent: 'flex-start',
              backgroundColor: window.innerWidth > 800 ? '#ffffff' : '#F1F3F9',
              borderColor: window.innerWidth > 800 ? '#ffffff' : '#F1F3F9',
              maxHeight: 0.6 * window.innerHeight,
              margin: 0,
              paddingRight: 5,
            }}
          >
            <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
              DATA DE DEFINIÇÃO:
          </label>
            <label
              autoComplete="off"
              className="input"
              placeholder="DATA"
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'DATA')}
              title="DATA DE DEFINIÇÃO DA PROPOSTA."
              onClick={() => selectInputInicio()}
              defaultValue={moment().format('DD/MM/YY')}
              style={{
                width: window.innerWidth > 800 ? 150 : 75,
              }}
              type="text"
              maxLength={5}
              id="inputInicio"
            >
              {dateInicio}
            </label>
            <DatePickerToMenus></DatePickerToMenus>
            <label className="title2" style={{ marginTop: 5, fontSize: 14 }}>
              PROPOSTA:
            </label>
            <textarea
              autoComplete="off"
              className="textarea"
              placeholder="PROPOSTA."
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'PROPOSTA.')}
              title="PROPOSTA."
              style={{
                width: '95%',
                minWidth: window.innerWidth > 800 ? 400 : 200,
                height: 100,
                minHeight: 100,
              }}
              type="text"
              maxLength={200}
              id="inputProposta"
            ></textarea>
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'row',
              marginTop: window.innerWidth > 800 ? 35 : 10,
              marginBottom: 0,
            }}
          >
            <button className="green-button" onClick={() => insertData()}>
              <img
                alt=""
                src={salvar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
            <button className="red-button" onClick={() => setviewcomponent(0)}>
              <img
                alt=""
                src={deletar}
                style={{
                  margin: 10,
                  height: 30,
                  width: 30,
                }}
              ></img>
            </button>
          </div>
        </div>
      </div>
    );
  } else if (viewcomponent === 2) { // atualizar.
    return (
      <div style={{
        justifyContent: 'flex-start',
        backgroundColor: '#F1F3F9',
        borderColor: '#F1F3F9',
        height: 350,
        margin: 0,
        paddingRight: 5,
      }}>
        <div
          className="secondary fade-in"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'fixed',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            margin: 0,
            zIndex: 5,
          }}
        >
          <DatePicker></DatePicker>
          <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
          <div
            className="secondary"
            style={{
              flexDirection: 'column',
              justifyContent: 'center',
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              padding: window.innerWidth > 800 ? 30 : 10,
            }}
          >
            <label className="title2" style={{ marginTop: 0, fontSize: 16 }}>
              ATUALIZAR PROPOSTA
          </label>
            <div
              className="scroll"
              style={{
                overflowY: window.innerWidth > 800 ? 'hidden' : 'scroll',
                justifyContent: 'flex-start',
                backgroundColor: window.innerWidth > 800 ? '#ffffff' : '#F1F3F9',
                borderColor: window.innerWidth > 800 ? '#ffffff' : '#F1F3F9',
                maxHeight: 0.6 * window.innerHeight,
                margin: 0,
                paddingRight: 5,
              }}
            >
              <label className="title2" style={{ marginTop: 15, fontSize: 14 }}>
                DATA DE DEFINIÇÃO:
              </label>
              <label
                autoComplete="off"
                className="input"
                placeholder="DATA"
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'DATA')}
                title="DATA DE DEFINIÇÃO DA PROPOSTA."
                onClick={() => selectInputInicio()}
                defaultValue={dateInicio}
                style={{
                  width: window.innerWidth > 800 ? 150 : 75,
                }}
                type="text"
                maxLength={5}
                id="inputInicio"
              >
                {dateInicio}
              </label>
              <DatePickerToMenus></DatePickerToMenus>
              <label className="title2" style={{ marginTop: 5, fontSize: 14 }}>
                PROPOSTA:
              </label>
              <textarea
                autoComplete="off"
                className="textarea"
                placeholder="PROPOSTA."
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'PROPOSTA.')}
                title="PROPOSTA."
                defaultValue={proposta}
                style={{
                  width: '95%',
                  minWidth: window.innerWidth > 800 ? 400 : 200,
                  height: 100,
                  minHeight: 100,
                }}
                type="text"
                maxLength={200}
                id="inputProposta"
              ></textarea>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                marginTop: window.innerWidth > 800 ? 35 : 15,
                marginBottom: 0,
              }}
            >
              <button className="green-button" onClick={() => updateData()}>
                <img
                  alt=""
                  src={salvar}
                  style={{
                    margin: 10,
                    height: 30,
                    width: 30,
                  }}
                ></img>
              </button>
              <button className="red-button" onClick={() => setviewcomponent(0)}>
                <img
                  alt=""
                  src={deletar}
                  style={{
                    margin: 10,
                    height: 30,
                    width: 30,
                  }}
                ></img>
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Propostas;