import './App.css';
import './design.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';

// importando os componentes (convertidos em funções para uso do react-hooks).
import Login from './components/Login';
import Atendimentos from './components/Atendimentos';
import Acolhimento from './components/Acolhimento';
import Secretaria from './components/Secretaria';
import Password from './components/Password';
import Hospitais from './components/Hospitais';
import Unidades from './components/Unidades';
import Pacientes from './components/Pacientes';
import Escala from './components/Escala';
import Usuarios from './components/Usuarios';
import Corrida from './components/Corrida';
import Toast from './components/Toast';
import InsertPaciente from './components/InsertPaciente';
import UpdateAtendimento from './components/UpdateAtendimento';
import Evolucao from './components/Evolucao';
import DatePicker from './components/DatePicker';

// importando o iddle-timeout.
import { useIdleTimer } from 'react-idle-timer';


function App() {

  const changeState = useDispatch();
  const viewLogin = useSelector((state) => state.login);
  var timer = 0;
  var interval = null;
  useEffect(() => {
    loadTimeOut();
    window.onmousemove = function () {
      resetTimeOut();
    }
  }, []);

  const loadTimeOut = () => {
    interval = setInterval(() => {
      timer = timer + 1;
      console.log('>> ' + timer);
      if (timer > 3000) {
        timer = 0;
        if (viewLogin === 1) {
          changeState({ type: 'LOGIN_ON' });
        } else {
          changeState({ type: 'LOGIN_ON' });
          setTimeout(() => {
            toast(1, '#ec7063', 'USUÁRIO DESLOGADO POR INATIVIDADE.', 3000);
          }, 3000);
        }
      }
    }, 1000);
  }

  const resetTimeOut = () => {
    clearInterval(interval);
    timer = 0;
    loadTimeOut();
  }

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time);
  }

  return (
    <div className="main"
    //onMouseMove={() => { resetTimer() }}
    //onMouseDown={() => { resetTimer() }}
    //onKeyPress={() => { resetTimer() }}
    >
      <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo} />
      <Login></Login>
      <Password></Password>
      <Secretaria></Secretaria>
      <Atendimentos></Atendimentos>
      <Acolhimento></Acolhimento>
      <Hospitais></Hospitais>
      <Unidades></Unidades>
      <Pacientes></Pacientes>
      <Escala></Escala>
      <Usuarios></Usuarios>
      <Corrida></Corrida>
      <Toast></Toast>
      <InsertPaciente></InsertPaciente>
      <UpdateAtendimento></UpdateAtendimento>
      <Evolucao></Evolucao>
      <DatePicker></DatePicker>
    </div>
  );
}

export default App;