import React, { useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import deletar from '../images/deletar.svg';
import salvar from '../images/salvar.svg';
import moment, { locale } from 'moment';

function UpdateAtendimento() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewUpdateAtendimento = useSelector((state) => state.updateatendimento);

  // recuperando estados atualizados.
  const idusuario = useSelector((state) => state.idusuario);
  const usuario = useSelector((state) => state.usuario);
  const usuariofirstname = useSelector((state) => state.usuariofirstname);
  const usuariolastname = useSelector((state) => state.usuariolastname);
  const tipo = useSelector((state) => state.tipo);
  const funcao = useSelector((state) => state.funcao);
  const hospital = useSelector((state) => state.nomehospital);
  const unidade = useSelector((state) => state.nomeunidade);
  const idatendimento = useSelector((state) => state.idatendimento);

  // estados relacionados ao atendimento.
  const [idpaciente, setidpaciente] = useState(0);
  const [box, setbox] = useState('');
  const [admissao, setadmissao] = useState('');
  const [dn, setdn] = useState('');
  const [nome, setnome] = useState('');
  const [peso, setpeso] = useState('');
  const [altura, setaltura] = useState('');
  const [antecedentes, setantecedentes] = useState('');
  const [alergias, setalergias] = useState('');
  const [medicacoes, setmedicacoes] = useState('');
  const [exames, setexames] = useState('');
  const [historia, sethistoria] = useState('');
  const [status, setstatus] = useState(0);
  const [ativo, setativo] = useState(0);
  const [classificacao, setclassificacao] = useState('');
  const [descritor, setdescritor] = useState('');
  const [precaucao, setprecaucao] = useState(0);
  const [assistente, setassistente] = useState('');
  // carregando o atendimento do paciente selecionado.
  const loadAtendimento = () => {
    axios.get(html + "/atendimentos").then((response) => {
      var y = [0, 1];
      y = response.data;
      var x = [0, 1];
      x = y.filter((value) => value.ativo !== 0 && value.hospital === hospital && value.unidade === unidade && value.id === idatendimento);
      setidpaciente(x.map((item) => item.idpaciente));
      setbox(x.map((item) => item.box));
      setadmissao(x.map((item) => item.admissao));
      setpeso(x.map((item) => item.peso));
      setaltura(x.map((item) => item.altura));
      setantecedentes(x.map((item) => item.antecedentes));
      setalergias(x.map((item) => item.alergias));
      setmedicacoes(x.map((item) => item.medicacoes));
      setexames(x.map((item) => item.exames));
      sethistoria(x.map((item) => item.historia));
      setstatus(x.map((item) => item.status));
      setativo(x.map((item) => item.ativo));
      setclassificacao(x.map((item) => item.classificacao));
      setdescritor(x.map((item) => item.descritor));
      setprecaucao(x.map((item) => item.precaucao));
      setassistente(x.map((item) => item.assistente));
      loadPaciente(x.map((item) => item.idpaciente));
    });
  }

  // carregando os dados do paciente.
  const loadPaciente = (idpaciente) => {
    axios.get(html + "/pacientes").then((response) => {
      var x = [0, 1];
      x = response.data;
      setnome(x.filter((item) => item.id == idpaciente).map((item) => item.nome));
      setdn(x.filter((item) => item.id == idpaciente).map((item) => item.dn));
    });
  }

  // preenchendo os campos.
  useEffect(() => {
    loadAtendimento();
    setshowassistente(0);
  }, [viewUpdateAtendimento]);

  // tela que define médico assistente.
  const [showassistente, setshowassistente] = useState();
  function ShowAssistente() {
    if (showassistente === 1) {
      return (
        <div className="menucover" style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          <div className="menucontainer" style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center' }}>
            <text className="title2" style={{ fontSize: 14 }}>{'ASSUMIR PACIENTE ' + nome + ' COMO MÉDICO ASSISTENTE?'}</text>
            <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'center', marginTop: 5 }}>
              <button className="green-button" onClick={() => { updateAtendimento(usuariofirstname + ' ' + usuariolastname) }} style={{ width: 100 }}>
                SIM
            </button>
              <button className="red-button" onClick={() => { updateAtendimento('SEM MÉDICO ASSISTENTE') }} style={{ width: 100 }}>
                NÃO
            </button>
            </div>
          </div>
        </div >
      );
    } else {
      return null;
    }
  }

  // função que atualiza o atendimento.
  const updateAtendimento = (value) => {
    var antecedentes = document.getElementById('inputAntecedentes').value.toUpperCase();
    var alergias = document.getElementById('inputAle').value.toUpperCase();
    var medicacoes = document.getElementById('inputMedicacoes').value.toUpperCase();
    var exames = document.getElementById('inputExames').value.toUpperCase();
    var historia = document.getElementById('inputHistoria').value.toUpperCase();
    var obj = {
      idpaciente: idpaciente,
      hospital: hospital,
      unidade: unidade,
      box: box,
      admissao: admissao,
      nome: nome,
      dn: dn,
      peso: peso,
      altura: altura,
      antecedentes: antecedentes,
      alergias: alergias,
      medicacoes: medicacoes,
      exames: exames,
      historia: historia,
      status: status,
      ativo: 2,
      classificacao: classificacao,
      descritor: descritor,
      precaucao: precaucao,
      assistente: value,
    };
    axios.post(html + '/updateatendimento/' + idatendimento, obj).then(() => {
      if (ativo == 1) {
        var conteudo =
          "DATA DE ADMISSÃO: " + admissao + " \n" +
          "ANTECEDENTES PESSOAIS: " + antecedentes + " \n" +
          "ALERGIAS: " + alergias + " \n" +
          "MEDICAÇÕES DE USO PRÉVIO: " + medicacoes + " \n" +
          "EXAMES PRÉVIOS RELEVANTES: " + exames + " \n\n" +
          "CONTEXTO: " + historia + " \n\n" +
          "DIAGNÓSTICOS: \n" +
          "ENUMERE AQUI AS HIPÓTESES DIAGNÓSTICAS."
        // inserindo o registro de admissão.
        var obj = {
          idpaciente: idpaciente,
          idatendimento: idatendimento,
          data: moment().format('DD/MM/YY HH:mm'),
          tipo: 'RELATÓRIO DE ADMISSÃO',
          texto: conteudo,
          idusuario: idusuario,
          usuario: usuariofirstname + ' ' + usuariolastname,
          status: 0,
        };
        axios.post(html + '/insertformulario', obj).then(() => {
          close();
        });
      } else {
        close();
      }
    });
  };

  const close = () => {
    changeState({
      type: 'CORRIDA_OFF',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuariofirstname,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
    changeState({
      type: 'CORRIDA_ON',
      // payloads.
      payloadIdusuario: idusuario,
      payloadNomeusuario: usuario,
      payloadNomeusuario: usuariofirstname,
      payloadFuncao: funcao,
      payloadTipo: tipo,
      payloadNomehospital: hospital,
      payloadNomeunidade: unidade,
      payloadIdatendimento: idatendimento,
    });
  };

  // varíavel 'status'.
  const clickStatus1 = () => {
    setstatus(1);
  };
  const clickStatus2 = () => {
    setstatus(2);
  };
  const clickStatus3 = () => {
    setstatus(3);
  };
  const clickStatus0 = () => {
    setstatus(0);
  };

  // renderização do componente.
  if (viewUpdateAtendimento === 1) {
    return (
      <div>
        <ShowAssistente></ShowAssistente>
        <div
          className="secondary fade-in"
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
          }}
        >
          <div>
            <div
              className="secondary"
              style={{
                backgroundColor: '#FFFFFF',
                borderRadius: 5,
                padding: 5,
              }}
            >
              <div
                className="title2"
                style={{ margin: 15, fontSize: 18 }}
              >
                ATUALIZAR ATENDIMENTO
              </div>
              <div
                className="scroll"
                style={{
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'flex-start',
                  height: 400,
                  width: window.innerWidth < 800 ? 0.9 * window.innerWidth : '',
                  margin: 5,
                  paddingRight: 5,
                }}
              >
                <div
                  id="IDENTIFICAÇÃO DO PACIENTE."
                  title="APENAS A SECRETÁRIA PODE MUDAR ESTES DADOS."
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'center',
                    alignItems: 'center',
                    margin: 2.5,
                  }}
                >
                  <div className="widget"
                    style={{
                      margin: 2.5, padding: 5, fontSize: 14,
                      width: window.innerWidth > 800 ? 120 : 50, height: 60, backgroundColor: '#279AB1'
                    }}>
                    {'BOX ' + box}
                  </div>
                  <div className="widget"
                    style={{
                      margin: 2.5, padding: 5, fontSize: 14,
                      width: window.innerWidth > 800 ? 300 : 180, height: 60, backgroundColor: '#279AB1'
                    }}>
                    {nome}
                  </div>
                  <div className="widget"
                    style={{
                      margin: 2.5, padding: 5, fontSize: 14,
                      width: window.innerWidth > 800 ? 120 : 50, height: 60, backgroundColor: '#279AB1'
                    }}>
                    {(moment().diff(moment(dn, 'DD/MM/YYYY'), 'years') + ' ANOS')}
                  </div>
                </div>
                <label
                  className="title2"
                  style={{
                    marginTop: 15,
                    fontSize: 14,
                    justifyContent: 'center',
                  }}
                >
                  STATUS:
                  </label>
                <div
                  id="STATUS DO PACIENTE."
                  style={{
                    display: 'flex',
                    flexDirection: window.innerWidth > 800 ? 'row' : 'column',
                    justifyContent: 'center',
                    margin: 5,
                  }}
                >
                  <div
                    className={status == 3 ? "green-button" : "blue-button"}
                    onClick={() => clickStatus3()}
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      padding: 10,
                      width: 110,
                      margin: 2.5,
                    }}
                  >
                    ESTÁVEL
                  </div>
                  <div
                    className={status == 2 ? "yellow-button" : "blue-button"}
                    onClick={() => clickStatus2()}
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      padding: 10,
                      width: 110,
                      margin: 2.5,
                    }}
                  >
                    ALERTA
                  </div>
                  <div
                    className={status == 1 ? "red-button" : "blue-button"}
                    onClick={() => clickStatus1()}
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      padding: 10,
                      width: 110,
                      margin: 2.5,
                    }}
                  >
                    INSTÁVEL
                  </div>
                  <div
                    className={status == 0 ? "grey-button" : "blue-button"}
                    onClick={() => clickStatus0()}
                    style={{
                      display: 'flex',
                      flexDirection: 'row',
                      padding: 10,
                      width: 110,
                      margin: 2.5,
                    }}
                  >
                    CONFORTO
                  </div>
                </div>
                <div
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'center',
                    marginTop: 0,
                  }}
                >
                  <div>
                    <label
                      className="title2"
                      style={{
                        width: window.innerWidth > 800 ? 350 : 135,
                        marginTop: 15,
                        fontSize: 14,
                        justifyContent: 'center',
                      }}
                    >
                      {window.innerWidth > 800 ? 'ANTECEDENTES PESSOAIS:' : 'ANT. PESSOAIS:'}
                    </label>
                    <textarea
                      className="textarea"
                      title="ANTECEDENTES PESSOAIS."
                      id="inputAntecedentes"
                      type="text"
                      defaultValue={antecedentes}
                      maxLength="200"
                      autoComplete="off"
                      placeholder="ANTECEDENTES PESSOAIS"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) =>
                        (e.target.placeholder = 'ANTECEDENTES PESSOAIS')
                      }
                      style={{
                        resize: 'none',
                        marginBottom: 0,
                        marginRight: 0,
                        width: window.innerWidth > 800 ? 350 : 135,
                        height: 90,
                      }}
                    ></textarea>
                  </div>
                  <div>
                    <label
                      className="title2"
                      style={{
                        width: window.innerWidth > 800 ? 350 : 135,
                        marginTop: 15,
                        fontSize: 14,
                        justifyContent: 'center',
                      }}
                    >
                      ALERGIAS:
                  </label>
                    <textarea
                      className="textarea"
                      title="ALERGIAS."
                      id="inputAle"
                      type="text"
                      defaultValue={alergias}
                      maxLength="200"
                      autoComplete="off"
                      placeholder="ALERGIAS"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) =>
                        (e.target.placeholder = 'ALERGIAS')
                      }
                      style={{
                        resize: 'none',
                        marginBottom: 0,
                        marginRight: 0,
                        width: window.innerWidth > 800 ? 350 : 135,
                        height: 90,
                      }}
                    ></textarea>
                  </div>
                </div>
                <div
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    justifyContent: 'center',
                  }}
                >
                  <div>
                    <label
                      className="title2"
                      style={{
                        width: window.innerWidth > 800 ? 350 : 135,
                        marginTop: 15,
                        fontSize: 14,
                        justifyContent: 'center',
                      }}
                    >
                      {window.innerWidth > 800 ? 'MEDICAÇÕES PRÉVIAS:' : 'MEDIC. PRÉVIAS:'}
                    </label>
                    <textarea
                      className="textarea"
                      title="MEDICAÇÕES PRÉVIAS."
                      id="inputMedicacoes"
                      type="text"
                      defaultValue={medicacoes}
                      maxLength="200"
                      autoComplete="off"
                      placeholder="MEDICAÇÕES PRÉVIAS"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) =>
                        (e.target.placeholder = 'MEDICAÇÕES PRÉVIAS')
                      }
                      style={{
                        resize: 'none',
                        marginBottom: 0,
                        marginRight: 0,
                        width: window.innerWidth > 800 ? 350 : 135,
                        height: 90,
                      }}
                    ></textarea>
                  </div>
                  <div>
                    <label
                      className="title2"
                      style={{
                        width: window.innerWidth > 800 ? 350 : 135,
                        marginTop: 15,
                        fontSize: 14,
                        justifyContent: 'center',
                      }}
                    >
                      {window.innerWidth > 800 ? 'EXAMES PRÉVIOS:' : 'EX. PRÉVIOS:'}
                    </label>
                    <textarea
                      className="textarea"
                      title="EXAMES PRÉVIOS."
                      id="inputExames"
                      type="text"
                      defaultValue={exames}
                      maxLength="200"
                      autoComplete="off"
                      placeholder="EXAMES PRÉVIOS"
                      onFocus={(e) => (e.target.placeholder = '')}
                      onBlur={(e) => (e.target.placeholder = 'EXAMES PRÉVIOS')}
                      style={{
                        resize: 'none',
                        marginBottom: 0,
                        width: window.innerWidth > 800 ? 350 : 135,
                        height: 90,
                      }}
                    ></textarea>
                  </div>
                </div>
                <div>
                  <label
                    className="title2"
                    style={{
                      marginTop: 15,
                      fontSize: 14,
                      justifyContent: 'center',
                    }}
                  >
                    HISTÓRIA DA DOENÇA ATUAL:
                  </label>
                  <textarea
                    className="textarea"
                    title="HISTÓRIA DA DOENÇA ATUAL."
                    id="inputHistoria"
                    type="text"
                    defaultValue={historia}
                    maxLength="200"
                    autoComplete="off"
                    placeholder="HISTÓRIA DA DOENÇA ATUAL"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) =>
                      (e.target.placeholder = 'HISTÓRIA DA DOENÇA ATUAL')
                    }
                    style={{
                      resize: 'none',
                      marginBottom: 5,
                      width: window.innerWidth > 800 ? 705 : 275,
                      height: 90,
                    }}
                  ></textarea>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  margin: 15,
                }}
              >
                <button
                  className="red-button"
                  onClick={() => close()}
                >
                  <img
                    alt=""
                    src={deletar}
                    style={{
                      margin: 10,
                      height: 30,
                      width: 30,
                    }}
                  ></img>
                </button>
                <button
                  className="green-button"
                  onClick={() => setshowassistente(1)}
                  style={{
                    marginRight: 0,
                  }}
                >
                  <img
                    alt=""
                    src={salvar}
                    style={{
                      margin: 10,
                      height: 30,
                      width: 30,
                    }}
                  ></img>
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default UpdateAtendimento;
