import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';

function Secretaria() {
  var html = 'https://pulsarapp-server.herokuapp.com';
  // var html = '//localhost:3001';

  const changeState = useDispatch();
  const viewSecretaria = useSelector((state) => state.secretaria);

  // recuperando estados atualizados.
  const loginid = useSelector((state) => state.idusuario);
  const loginnome = useSelector((state) => state.nomeusuario);
  const logintipo = useSelector((state) => state.tipo);

  // selecionando a tela de atendimentos (apenas secretária).
  const selectAtendimento = (item) => {
    changeState({
      type: 'ATENDIMENTO_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadTipo: logintipo,
    });
  }

  // selecionando a tela de hospitais.
  const selectHospitais = (item) => {
    changeState({
      type: 'HOSPITAL_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadTipo: logintipo,
    });
  }

  // HEADER.
  const clickLogin = () => {
    changeState({ type: 'LOGIN_ON' });
  };
  // função para acessar a tela Password.
  const clickPassword = () => {
    changeState({
      type: 'PASSWORD_ON',
      // payloads.
      payloadIdusuario: loginid,
      payloadNomeusuario: loginnome,
      payloadTipo: logintipo,
    });
  };
  // function Header.
  function Header() {
    return (
      <div
        id="HEADER"
        style={{
          display: 'flex',
          flexDirection: 'row',
          justifyContent: 'center',
          alignItems: 'center',
          margin: 0,
          padding: 5,
          width: '100%',
          height: 125,
        }}
      >
        <div
          className="secondary"
          style={{
            display: 'flex',
            flex: 1,
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            height: 125,
            width: 125,
            maxWidth: 125,
          }}
        >
          <img
            alt=""
            id="logo"
            src={logo}
            style={{
              margin: 0,
              height: '80%',
            }}
          ></img>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
            width: '100%',
          }}
        >
          <p className="title1" style={{ color: '#1F7A8C', height: 115 }}>
            {'PAINEL ADMINISTRATIVO'}
          </p>
        </div>
        <div
          className="secondary"
          style={{
            display: 'flex',
            flexDirection: 'column',
            backgroundColor: '#E1E5F2',
            borderRadius: 5,
          }}
        >
          <button
            className="blue-button"
            title="CLIQUE PARA ALTERAR SUA SENHA."
            onClick={() => clickPassword()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginBottom: 0,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            OLÁ, {loginnome}.
                    </button>
          <button
            className="blue-button"
            title="FAZER LOGOFF."
            onClick={() => clickLogin()}
            style={{
              display: 'flex',
              width: 200,
              margin: 10,
              marginTop: 5,
              paddingLeft: 20,
              paddingRight: 20,
            }}
          >
            SAIR
            </button>
        </div>
      </div>
    );
  }

  // exibindo card para gerenciamento de atendimentos.
  function Atendimentos() {
    if (logintipo === 3) {
      return (
        <div
          className="blue-button"
          style={{ height: 280, width: 280, flexDirection: 'column' }}
          onClick={() => selectAtendimento()}>
          <div className="title2" style={{ color: '#ffffff', fontSize: 18 }}>
            {'ATENDIMENTOS'}
          </div>
          <div className="title2" style={{ color: '#ffffff', fontSize: 14, width: 200, opacity: 0.6, textAlign: 'center' }}>
            {'CADASTRO E MOVIMENTAÇÃO DE PACIENTES'}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // exibindo card para gerenciamento de escalas.
  function Hospitais() {
    if (logintipo === 3) {
      return (
        <div
          className="blue-button"
          style={{ height: 280, width: 280, flexDirection: 'column' }}
          onClick={() => selectHospitais()}>
          <div className="title2" style={{ color: '#ffffff', fontSize: 18 }}>
            {'ESCALAS'}
          </div>
          <div className="title2" style={{ display: logintipo === 'ENF' ? 'none' : 'flex', color: '#ffffff', fontSize: 14, width: 200, opacity: 0.6, textAlign: 'center' }}>
            {'GERENCIAMENTO DA ESCALA DOS PLANTONISTAS'}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // renderização do componente.
  if (viewSecretaria === 1) {
    return (
      <div
        className="main fade-in"
        style={{
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'center',
          width: '100%',
        }}
      >
        <Header></Header>
        <div
          style={{
            display: 'flex',
            flexDirection: 'row',
            justifyContent: 'center',
            alignItems: 'center',
            minHeight: window.innerHeight - 155,
            maxHeight: window.innerHeight - 155,
          }}>
          <Atendimentos></Atendimentos>
          <Hospitais></Hospitais>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Secretaria;
